<template>
    <div class="main-page">
        <div class="headerTag">{{$route.query.BillType==1?"出库单":"入库单"}}</div>
        <form class="h-form">
            <item must name="仓库名称">
                <input v-model="info.WarehouseName" readonly @click="choosePerson" />
            </item>
            <item must name="数量">
                <input v-model="info.Qty" />
            </item>
            <item must name="类型">
                <van-radio-group v-model="info.BillType" disabled>
                    <van-radio :name="1">出库单</van-radio>
                    <van-radio :name="3">入库单</van-radio>
                </van-radio-group>
            </item>
            <item must name="日期">
                <input v-model="info.FDate" readonly @click="chooseTime('FDate')" />
            </item>
        </form>
        <van-button type="primary" block size="normal" :color="$config.theme" style="width: calc(100% - 30px); margin:3px 15px" @click="submit" :loading="loading">提交</van-button>
        <van-popup v-model="timeShow" position="bottom">
            <van-datetime-picker type="date" title="选择日期" @confirm="confirmTime" />
        </van-popup>

    </div>
</template>

<script>
export default {
    name: "StockBillAE",
    props: {},
    components: {},
    data() {
        return {
            apis: {
                edit: this._api.OSMS_StockBillEdit,
                create: this._api.OSMS_StockBillCreate,
                list: this._api.OSMS_StockBillManage,
                delete: this._api.OSMS_StockBillDelete,
                audit: this._api.OSMS_StockBillAudit,
                details: this._api.OSMS_StockBillDetails
            },
            loading: false,
            curId: null,
            info: {
                PersonName: "",
                FName: "",
                EntryList: []
            },
            rules: {
                WarehouseName: "请选择仓库！",
                Qty: "请输入数量！",
                FDate: "请选日期！"
            },
            curTimeFieldName: "",
            timeShow: false
        }
    },
    methods: {
        async submit() {
            await this.$u.checkForm(this.info, this.rules)
            this.loading = true
            let apiFun = this.curId ? this.apis.edit : this.apis.create
            apiFun(this.info).then(res => {
                if (res) {
                    this.$u.msg(this.info.Id ? "编辑成功！" : "创建成功！")
                    setTimeout(() => {
                        this.$router.back()
                    }, 200);
                }
                this.loading = false
            })
        },
        chooseTime(fieldName) {
            this.curTimeFieldName = fieldName
            this.timeShow = true
        },
        confirmTime(time) {
            this.timeShow = false
            this.info[this.curTimeFieldName] = this.$time(time).format("YYYY-MM-DD")
        },
        choosePerson() {
            window.temFun = (warehouse) => {
                this.$set(this.info, "WarehouseName", warehouse.WarehouseName)
                this.$set(this.info, "WarehouseId", warehouse.Id)
            }
            this.$router.push(`/modular/oa/WarehouseChoose`)
        }
    },
    mounted() {
        this.info = {
            BillType: Number(this.$route.query.BillType)
        }
        this.curId = this.$route.query.Id
    }
}
</script>

<style lang="scss" >
.van-radio-group {
    display: flex;
}
</style>